<script >
import './css/index.css'
import DetailsContent from './module/homePage'
import PersonContent from './module/person '
import WorkContent from './module/work'
import SkillContent from './module/skill'
import ProjectContent from './module/project'
// import EducationContent from './module/education'
import MyselfContent from './module/myself'
// import Message from './module/message'
// import  {Phone,MapLocation,Comment}  from "@element-plus/icons";
export default ({
    name:'',
    components: {
        DetailsContent,
        PersonContent,
        WorkContent,
        SkillContent,
        ProjectContent,
        // EducationContent,
        MyselfContent,
        // Message,
    },
    data(){
        return{
            pageCount:1,
        }
    },
    created() {
        // 缩放
        let t = window.devicePixelRatio   // 获取下载的缩放 125% -> 1.25    150% -> 1.5

        if (!!window.ActiveXObject || "ActiveXObject" in window) {
            if (t != 1) {
                // 如果在笔记本中用IE浏览器打开 则弹出提示
                alert('您的设备对显示进行放大导致页面显示不完全,请调整后打开/或用其他浏览器')
            }
        } else {
            if (t != 1) {   // 如果进行了缩放，也就是不是1
                let c = document.querySelector('body')
                c.style.zoom = -0.62 * t + 1.55;   // 就去修改页面的缩放比例，这个公式我自己算的，不准确，勉强。
            }
        }

    },
	mounted (){
        this.followMouse()
    },
    methods:{
        changePage(ind){
            this.pageCount = ind
            console.log(this.pageCount);
        },
        followMouse(){
            // 鼠标跟随
            let img = document.querySelector('.img')
            // 定义小图片的旋转角度
            let deg = 0
            // 定义小图片位于网页左侧的位置
            let imgx = 0
            // 定义小图片位于网页顶部的位置
            let imgy = 0
            // 定义小图片x轴的位置
            let imgl = 0
            // 定义小图片y轴的位置
            let imgt = 0
            // 定义小图片翻转的角度
            let y = 0 
            // 定义一个计数器
            let index = 0

            window.addEventListener('mousemove',function(xyz){
                // 获取网页左侧距离的图片位置
                imgx = xyz.x - img.offsetLeft - img.clientWidth /2-30 //避免与鼠标重叠影响mouseover事件
                // 多去网页顶部距离图片的位置
                imgy = xyz.y - img.offsetTop - img.clientHeight /2
                // 套入公式，定义图片的旋转角度
                deg = 360*Math.atan(imgy/imgx)/(2*Math.PI)
                // 每当鼠标移动的时候重置index
                index= 0
                // 定义当前鼠标的位置
                let x = event.clientX
                // 当鼠标的x轴大于图片的时候，提普安就要对着鼠标，所以需要将图片翻转过来
                // 否则就不用翻转
                if(img.offsetLeft<x){
                    y=-180
                }else{
                    y=0
                }
            })
            setInterval(()=>{
                // 设置小图片的旋转和翻转
                img.style.transform = "rotateZ("+deg+"deg) rotateY("+y+"deg)"
                index++
                // 在这里设置小图片的位置和速度，并判断小图片到达鼠标位置时停止移动
                if(index<50){
                    imgl+=imgx/50
                    imgt+=imgy/50
                }
                img.style.left = imgl+"px"
                img.style.top = imgt+"px"
            },10)
        }
    }
})
</script>
